.root {
  font-family: Caveat, cursive;
  font-size: 20px;

  max-width: 800px;
  padding: 10px;
  margin: 20px;

  border: 1px solid gray;
  border-radius: 2px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);

  .title {
    margin: 0;
  }

  .date {
    margin-bottom: 10px;
    margin-right: 10px;
  }
}